body {
  font-family: 'Roboto', sans-serif;
  color: #333;
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

*, *::before, *::after {
  box-sizing: inherit; }

a {
  color: #d59541; }

.large-hero {
  position: relative;
  border-bottom: solid 10px #2f5572; }
  .large-hero__image {
    display: block; }
  .large-hero__text-content {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    text-align: center;
    transform: translateY(-50%); }
  .large-hero__title {
    font-weight: 300;
    color: #2f5572;
    font-size: 2.4rem;
    margin: 0; }
    @media (min-width: 530px) {
      .large-hero__title {
        font-size: 4.8rem; } }
  .large-hero__subtitle {
    font-weight: 300;
    color: #2f5572;
    font-size: 1.5rem;
    margin: 0; }
    @media (min-width: 530px) {
      .large-hero__subtitle {
        font-size: 2.9rem; } }
  .large-hero__description {
    color: #fff;
    font-size: 1.1rem;
    font-weight: 100;
    text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.1);
    max-width: 30rem;
    margin-left: auto;
    margin-right: auto; }
    @media (min-width: 530px) {
      .large-hero__description {
        font-size: 1.875rem; } }

.btn {
  background-color: #2f5572;
  color: #fff;
  text-decoration: none;
  padding: .75rem 1.2rem;
  display: inline-block; }
  .btn--orange {
    background-color: #d59541; }
  .btn--large {
    padding: 1.1rem 1.9rem; }
    @media (min-width: 530px) {
      .btn--large {
        font-size: 1.25rem; } }

.wrapper {
  padding-left: 18px;
  padding-right: 18px;
  max-width: 1236px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden; }
  .wrapper--medium {
    max-width: 976px; }
  .wrapper .wrapper {
    padding-left: 0;
    padding-right: 0; }
  .wrapper--b-margin {
    margin-bottom: 1rem; }
    @media (min-width: 530px) {
      .wrapper--b-margin {
        font-size: 3.875rem; } }

.page-section {
  padding: 1.2rem 0; }
  @media (min-width: 800px) {
    .page-section {
      padding: 4.8rem 0; } }
  .page-section--blue {
    background-color: #2f5572;
    color: #fff; }

.headline {
  font-weight: 300;
  color: #2f5572;
  font-size: 1.9rem; }
  @media (min-width: 530px) {
    .headline {
      font-size: 2.875rem; } }
  .headline--centered {
    text-align: center; }
  .headline--orange {
    color: #d59541; }
  .headline--small {
    font-size: 1.2rem; }
    @media (min-width: 530px) {
      .headline--small {
        font-size: 1.875rem; } }
  .headline--narrow {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto; }
  .headline--light {
    font-weight: 100; }
  .headline--b-margin-small {
    margin-bottom: .5em; }
  .headline--b-margin-large {
    margin-bottom: 1.6em; }
  .headline--no-t-margin {
    margin-top: 0; }

.row::after {
  content: '';
  clear: both;
  display: table; }

.row__b-margin-until-medium {
  margin-bottom: 1rem; }

@media (min-width: 800px) {
  .row__b-margin-until-medium {
    margin-bottom: 0; }
  .row__medium-4 {
    float: left;
    width: 33.33%; }
  .row__medium-4--larger {
    width: 37%; }
  .row__medium-8 {
    float: left;
    width: 66.66%; }
  .row__medium-8--smaller {
    width: 63%; } }

.row--gutters {
  margin-right: -65px; }

.row--gutters > div {
  padding-right: 65px; }

.generic-content-container p {
  line-height: 1.65;
  margin: 1.8rem 0; }
  @media (min-width: 530px) {
    .generic-content-container p {
      font-size: 1.125rem; } }
  .generic-content-container p a {
    font-weight: 700; }
